import Vue from 'vue'
import * as firebase from "firebase/app";
import "firebase/messaging";

firebase.initializeApp({
  apiKey: "AIzaSyDqN6_Q--78FpvzVvYVof0f6icQsXMYehw",
  authDomain: "moneyshield-c3f1d.firebaseapp.com",
  databaseURL: "https://moneyshield-c3f1d.firebaseio.com",
  projectId: "moneyshield-c3f1d",
  storageBucket: "moneyshield-c3f1d.appspot.com",
  messagingSenderId: "630604493528",
  appId: "1:630604493528:web:1fdd4d86328e381203ddad",
  measurementId: "G-Q058DTDN27"
});

let messaging = null;

if (firebase.messaging.isSupported()) {
  console.info('[info] Firebase Message 를 지원합니다.');
  messaging = firebase.messaging();
  messaging.usePublicVapidKey("BMia7neF4wXywKcRXlX7__aD4YRcD2pl1OpMNd8t42eQ2BTUW4fmjVnFHxHPCk_76n7uUNw71Z9QDYvXH2WFwuo");
  Vue.prototype.$message = messaging;
}

const setToken = () => {
  messaging.getToken()
  .then((token) => {
    console.info('[info] Firebase Message accesstoken : ' + token);
    if (token) localStorage.setItem(btoa(escape('moneyshield')), token);
    else localStorage.removeItem(btoa(escape('moneyshield')));
  });
};

import { register } from 'register-service-worker'

if (process.env.NODE_ENV === 'production') {
  register(`${process.env.BASE_URL}service-worker.js`, {
    ready () {
      console.info('[info] 서비스워커가 준비되었습니다.');
    },
    registered () {
      console.info('[info] 서비스워커가 등록되었습니다.');

      if (firebase.messaging.isSupported() && 'Notification' in window) {
        console.info('[info] Notification 을 지원합니다.');

        if (Notification.permission !== 'granted') {
          Notification.requestPermission()
          .then((permission) => {
            console.info("[info] Notification permission is '" + permission + "'.");
            if (permission == 'granted') setToken();
          });
        }else{
          setToken();
        }
      }
    },
    cached () {
      console.info('[info] 서비스워커가 캐시되었습니다.');
    },
    updatefound () {
      console.info('[info] 서비스워커 업데이트가 대기중 입니다.');
    },
    updated () {
      console.info('[info] 서비스워커가 업데이트 되었습니다.');
    },
    offline () {
      console.info('[info] 서비스워커가 오프라인상태 입니다.');
    },
    error (error) {
      console.error('[info] 서비스워커 에러. ' + error);
    }
  })
}
